import React, { Component } from 'react'

// FUNCTION BASED CHILD COMPONENT
function GalleryComponent(props){
    
    return(
        <div>
            <p>{props.ginfo.title}</p>
            <img src={props.ginfo.url} />
        </div>
    );
}


