import React, { Component } from 'react'
import galleryApi from '../Api/galleryApi'
import GalleryComponent from './GalleryComponent'

/* map data using api with state and using class not function */

class Gallery extends Component{

    constructor(){
        super()
        this.state = {
            galleryInfo: galleryApi
        }
    }

    render(){
        
     //dont use key={id} otherwise getting error - variable not defined   
    //const infoAbout = this.state.aboutInfo.map(info => <AboutComponent key={info.id} info={info} />)
    const infoGallery = this.state.galleryInfo.map(ginfo => <GalleryComponent ginfo={ginfo} />)
      
          return(
            <div>
                {infoGallery}
            </div>
        );
    }
}



/* map data using api without state (using function not class) */

