import React, { Component } from 'react'
import userInfo from '../Api/userInfo'
import LoginComponent from './LoginComponent'
import Header from '../Header/Header'
import Footer from '../Footer'
import Home from '../Home/Home'


/* map data using api with state and using class not function */

class Login extends Component{

    constructor(){
        super()
        this.state = {
            loginInfo: userInfo,
            isLoggedin:true
        }
        // this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
    }

    // handleChange(event){
    //     const { name, value} = event.target
    //     const slogin = this.loginInfo.pwd
        
    //     this.setState({
    //         slogin : value
    //     })
    // }

    handleSubmit(event){
        event.preventDefault()
        // const suname = this.loginInfo.uname
        // const spwd = this.loginInfo.pwd
        // const rsl = suname =spwd
        // console.log(rsl)

        // if(rsl){
        //     alert("enter")
        // }else{
        //     alert ("error")
        // }
        
    }
     

    render(){
        
    const infoLogin = this.state.loginInfo.map(uinfo => <LoginComponent key={uinfo.id} uinfo={uinfo} handleChange={this.handleChange}/>)
   
          return(
            <div>
                
                <Header />
                 {/* {infoLogin} */}
                  {this.state.isLoggedin === false ? <Home /> : <div>{infoLogin}</div>}
                <Footer />
            </div>
        );  
    }
}



