import React, { Component } from 'react'



// FUNCTION BASED CHILD COMPONENT
function LoginComponent(props){

    
    return(
        <div>
          <form onSubmit ={props.handleSubmit}>
              <input 
                type = "text" 
                name = "uname"
                placeholder = "type usename"
                
                
              />
              <br />
              <input 
                type = "password" 
                name = "pwd"
                placeholder = "type password"
                
                
              />
              <br />
              <button>Submit</button>
          </form>
        </div>
    );
}


