import React, { Component } from 'react'
import aboutApi from '../Api/aboutApi'
import AboutComponent from './AboutComponent'

/* map data using api with state and using class not function */

class About extends Component{

    constructor(){
        super()
        this.state = {
            aboutInfo: aboutApi
        }
    }

    render(){
        
     //dont use key={id} otherwise getting error - variable not defined   
    //const infoAbout = this.state.aboutInfo.map(info => <AboutComponent key={info.id} info={info} />)
    const infoAbout = this.state.aboutInfo.map(info => <AboutComponent info={info} />)
      
          return(
            <div>
                {infoAbout}
            </div>
        );
    }
}



/* map data using api without state (using function not class) */

